.navbar_nomadi {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
    padding-bottom: 0.5em;
}

.navbar_nomadi h2 {
    font-family: 'Comfortaa', cursive;
    font-size: 30px;
    color: white;
    display: flex;
    align-items: center;
}

.navbar_nomadi h2 span {
    color: #1aa5ff;
}

.home {
    border: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-image: url(/static/media/moving-box.9bd71fac.jpg);
    background-size: cover;
    /* filter: brightness(0.8); */
    border-radius: 0;
    overflow: hidden;
}
.buttons-home {
    border: 0;
    height: 30vh;
    width: 50%;
    display: flex;
    justify-content: space-around;
    max-width: 500px;
    flex-direction: column;
    align-items: center;
    padding: 5%;
}

.title-home {
    text-align: center;
    font-size: 50px;
    color: white;
    width: auto;
    height: auto;
}


@media (max-width: 725px) {
    .home {
        background-image: none;
    }

     .buttons-home {
        width: 100%;
        margin-bottom: 25%;
    }

    .title-home {
        width: 80%;
    }
}

@media (max-width: 1024px) {
    .buttons-home {
        height: 15vh;
    }
}
#component-button-next {
    background-color: #4cd1a0;
    color: #fff;
    font-size: 20px;
    justify-content: center;
    border-radius: 8px;
    margin-top: 10px;
    border: none;
}

#component-button-next:hover {
    background-color: #1aa5ff;
}
.input {
    font-size: 15px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-start;
}

.input input {
    width: 100%;
}

.input label {
    color: #3a3939;
}
  
